const View = () => {
    const {animal} = useParams();
    const animalIndex = FORTUNES.findIndex(f => Object.keys(f)[0] === `${animal}`);
    const dataIndex = (~~(+new Date / 86400000) + animalIndex) % DATA.length;

    return <div className="view">
        <div className={`bg ${animal}`}></div>
        <div className="content">
            <h2 style={{borderLeftColor: "#" + Math.floor(Math.random()*16777215).toString(16)}}>{moment().format('YYYY')} {moment().format('M')} {moment().format('D')} '{Object.values(FORTUNES[animalIndex])}'  .</h2>
            <div className="p" style={{borderLeftColor: "#" + Math.floor(Math.random()*16777215).toString(16)}}>    <blockquote>{DATA[dataIndex].number}</blockquote><br/> <blockquote>{DATA[dataIndex].initial}</blockquote>  .</div>
            <ul style={{borderLeftColor: "#" + Math.floor(Math.random()*16777215).toString(16)}}>
                {
                    DATA[dataIndex].trade.split(".").map((s, i) => s.replace(/\s/g, "").length === 0 ? '' : <li key={`${s}${i}`}>{s}.</li>)
                }
            </ul>
            <div style={{overflow: 'hidden', marginTop: '1.5em'}}>
                <GoogleAdsense
                    adClient='ca-pub-5809567310705589'
                    adSlot='5794652355'
                    style={{display: 'block', textAlign: 'center'}}
                    adFormat='horizontal'
                    fullWidthResponsive='true'
                />
            </div>
        </div>
        <Link className="back" to={`/fortunes`}></Link>
    </div>;
};

const App = () => {
  return <Router history={createBrowserHistory()}>
    <Provider store={store}>
        {/*<BrowserRouter basename="/tistory/2005774/skin/images">*/}
        <HashRouter>
            <Switch>
                <Route path="/fortunes/:animal" component={View} />
                <Route path="/fortunes">
                    <div className="wrap">
                        {
                            FORTUNES.map((fortune, idx) => {
                                const key = `fortunes_${idx}`;
                                const to = `/fortunes/${Object.keys(FORTUNES[idx])}`;
                                const className = `${Object.keys(FORTUNES[idx])}`;
                                const title = `${Object.values(FORTUNES[idx])}  `;

                                return <Link key={key} to={to} className={className} title={title}>{title}</Link>
                            })
                        }
                    </div>
                </Route>
                <Route path="*">
                    <Link to="/fortunes" className="main">     </Link>
                </Route>
            </Switch>
        </HashRouter>
        {/*</BrowserRouter>*/}
    </Provider>
  </Router>;
}
export default App;
